.custom-nav-link.active {
  background-color: #3F3F3F;
  color: white;
  transition: border-color 0.3s ease-in-out;
}

.custom-nav-link {
  background-color: #131313;
  color: #6E6E6E;
  font-weight: 500;
  height: 41px;
  text-align: center;

  &:not(.active):hover {
    background-color: #4f4f4f;
    color: black;
  }
}

.dropdown-container {
  margin: 0 -20px -20px -20px;
  background-color: #131313 !important;
}

#settingButton {
  margin-top: 9px;
  background-color: transparent;
  height: fit-content;
  width: fit-content;
}

#settingButton svg path {
  transition: fill 0.3s ease;
}

#settingButton:hover svg path {
  fill: #ffb255;
}


.header-container {
  position: relative;
  width: 250px;
  height: 60px;
}

.image-header {
  position: absolute;
  top: -28px;
  right: -15px;
}